"use client"

import { zodResolver } from "@hookform/resolvers/zod"
import { useForm } from "react-hook-form"
import * as z from "zod"
import { Button } from "@/components/ui/button"
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
} from "@/components/ui/form"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { CheckboxCard } from "@/components/ui/checkbox-card"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"

const formSchema = z.object({
  workflow_id: z.string({
    required_error: "Please select a workflow.",
  }),
  env_strategy: z.string({
    required_error: "Please select an environment selection strategy.",
  }),
  trace_level: z.string({
    required_error: "Please select a trace level.",
  }),
  environments: z.array(z.string()).min(1, {
    message: "Please select at least one execution environment.",
  }),
})

export default function WorkflowCreatePage() {
  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      workflow_id: "1",
      env_strategy: "start",
      trace_level: "med",
      environments: [],
    },
  })

  function onSubmit(values: z.infer<typeof formSchema>) {
    console.log(values)
    // Submit the form data to your API
  }

  return (
    <div className="container mx-auto py-10">
      <Card className="custom-border border-gray-900/30/40 bg-black">
        <CardHeader>
          <CardTitle className="text-2xl font-medium">Create Workflow Execution</CardTitle>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
              <FormField
                control={form.control}
                name="workflow_id"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Workflow</FormLabel>
                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                      <FormControl>
                        <SelectTrigger className="bg-gray-900/10 backdrop-blur-md  border-gray-800 focus:ring-gray-700">
                          <SelectValue placeholder="Select a workflow" />
                        </SelectTrigger>
                      </FormControl>
                      <SelectContent className="bg-gray-900/10 backdrop-blur-md  border-gray-800">
                        <SelectItem value="1">Preprocess Sensor Data</SelectItem>
                        <SelectItem value="2">Reticulate Splines</SelectItem>
                        <SelectItem value="3">Reticulate Splines (Parallelized)</SelectItem>
                      </SelectContent>
                    </Select>
                    <FormDescription>
                      Select the root workflow to be executed
                    </FormDescription>
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="env_strategy"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Environment Selection Strategy</FormLabel>
                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                      <FormControl>
                        <SelectTrigger className="bg-gray-900/10 backdrop-blur-md  border-gray-800 focus:ring-gray-700">
                          <SelectValue placeholder="Select a strategy" />
                        </SelectTrigger>
                      </FormControl>
                      <SelectContent className="bg-gray-900/10 backdrop-blur-md  border-gray-800">
                        <SelectItem value="start">Earliest Start</SelectItem>
                        <SelectItem value="time">Fastest Estimated Execution Time</SelectItem>
                        <SelectItem value="cost">Least Expensive</SelectItem>
                      </SelectContent>
                    </Select>
                    <FormDescription>
                      Select the top priority to be used when selecting an execution environment
                    </FormDescription>
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="trace_level"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Trace Level and Utilization Measurement</FormLabel>
                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                      <FormControl>
                        <SelectTrigger className="bg-gray-900/10 backdrop-blur-md  border-gray-800 focus:ring-gray-700">
                          <SelectValue placeholder="Select a trace level" />
                        </SelectTrigger>
                      </FormControl>
                      <SelectContent className="bg-gray-900/10 backdrop-blur-md  border-gray-800">
                        <SelectItem value="min">Minimal - Step Start/End Times Only</SelectItem>
                        <SelectItem value="med">Standard - Step Start/End Time and Avg Utilization</SelectItem>
                        <SelectItem value="debug">Maximum - Debug level traces</SelectItem>
                      </SelectContent>
                    </Select>
                    <FormDescription>
                      Select how detailed the logging and instrumentation for the execution should be
                    </FormDescription>
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="environments"
                render={() => (
                  <FormItem>
                    <FormLabel>Compatible Execution Environments</FormLabel>
                    <div className="space-y-4 mt-2">
                      <FormField
                        control={form.control}
                        name="environments"
                        render={({ field }) => {
                          return (
                            <FormItem>
                              <FormControl>
                                <CheckboxCard
                                  id="cb_env_1"
                                  checked={field.value?.includes("aws")}
                                  onCheckedChange={(checked: boolean) => {
                                    return checked
                                      ? field.onChange([...field.value, "aws"])
                                      : field.onChange(
                                          field.value?.filter(
                                            (value) => value !== "aws"
                                          )
                                        )
                                  }}
                                  label="AWS (Virginia)"
                                  details={
                                    <>
                                      <div>188 / 256 CPU Cores Available</div>
                                      <div>6144 / 8192 Gb CPU Memory Available</div>
                                      <div>520 / 1024 GPU Cores Available</div>
                                      <div>7168 / 8192 Gb GPU Memory Available</div>
                                    </>
                                  }
                                />
                              </FormControl>
                            </FormItem>
                          )
                        }}
                      />
                      <FormField
                        control={form.control}
                        name="environments"
                        render={({ field }) => {
                          return (
                            <FormItem>
                              <FormControl>
                                <CheckboxCard
                                  id="cb_env_2"
                                  checked={field.value?.includes("dgx")}
                                  onCheckedChange={(checked: boolean) => {
                                    return checked
                                      ? field.onChange([...field.value, "dgx"])
                                      : field.onChange(
                                          field.value?.filter(
                                            (value) => value !== "dgx"
                                          )
                                        )
                                  }}
                                  label="DGX Cloud (Oracle) (California)"
                                  details={
                                    <>
                                      <div>248 / 512 CPU Cores Available</div>
                                      <div>8192 / 32768 Gb CPU Memory Available</div>
                                      <div>1012 / 4096 GPU Cores Available</div>
                                      <div>52428.8 / 65536 Gb GPU Memory Available</div>
                                    </>
                                  }
                                />
                              </FormControl>
                            </FormItem>
                          )
                        }}
                      />
                    </div>
                    <FormDescription>
                      Select the environments to consider for execution
                    </FormDescription>
                  </FormItem>
                )}
              />

              <Button 
                type="submit" 
                className="bg-primary hover:bg-primary/90 text-primary-foreground"
              >
                Dispatch For Execution
              </Button>
            </form>
          </Form>
        </CardContent>
      </Card>
    </div>
  )
} 