"use client"

import * as React from "react"
import { CheckIcon } from "@radix-ui/react-icons"
import * as CheckboxPrimitive from "@radix-ui/react-checkbox"

import { cn } from "@/lib/utils"

const CheckboxCard = React.forwardRef<
  React.ElementRef<typeof CheckboxPrimitive.Root>,
  React.ComponentPropsWithoutRef<typeof CheckboxPrimitive.Root> & {
    label: string;
    details?: React.ReactNode;
  }
>(({ className, label, details, ...props }, ref) => (
  <div className="flex flex-col space-y-2 border border-gray-800 rounded-md p-4 hover:border-gray-700 transition-colors">
    <div className="flex items-start">
      <CheckboxPrimitive.Root
        ref={ref}
        className={cn(
          "peer h-5 w-5 shrink-0 rounded border border-gray-700 bg-gray-900/10 backdrop-blur-md  ring-offset-background focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 data-[state=checked]:bg-primary data-[state=checked]:text-primary-foreground",
          className
        )}
        {...props}
      >
        <CheckboxPrimitive.Indicator
          className={cn("flex items-center justify-center text-current")}
        >
          <CheckIcon className="h-4 w-4" />
        </CheckboxPrimitive.Indicator>
      </CheckboxPrimitive.Root>
      <label
        htmlFor={props.id}
        className="ml-2 text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
      >
        {label}
      </label>
    </div>
    {details && (
      <div className="ml-7 text-xs text-gray-400 space-y-1">
        {details}
      </div>
    )}
  </div>
))
CheckboxCard.displayName = "CheckboxCard"

export { CheckboxCard } 